.c-btn {
  display: inline-block;
  background: #222;
  color: #fff;
  font-weight: 700;
  line-height: 1.4375;
  padding: 1.5625rem;
  width: 100%;
  max-width: 17.5rem;
  text-align: center;
  position: relative;
  transform: translate(-0.375rem, -0.375rem);
  transition: 0.3s transform;
}

.c-btn::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  border-bottom: 1px #222 solid;
  border-right: 1px #222 solid;
  top: 0.375rem;
  left: 0.375rem;
  transition: 0.3s top, 0.3s left;
}

.c-btn::after {
  position: absolute;
  content: "";
  background-image: url(../img/icon_arrow-right.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 0.5rem;
  height: 0.875rem;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
}

.c-btn:hover {
  transform: initial;
}

.c-btn:hover::before {
  top: 0;
  left: 0;
}

.c-ribbon {
  position: absolute;
  content: "";
  top: 0.5rem;
  left: -0.5rem;
  background: #382620;
  color: #fff;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 1.41667;
  padding: 0.1875rem 0.875rem;
}

.c-ribbon::before {
  position: absolute;
  content: "";
  top: 100%;
  left: 0;
  border: none;
  border-bottom: solid 0.3125rem transparent;
  border-right: solid 0.5rem #aaa;
  /*折り返し部分*/
}

.c-ribbon::after {
  position: absolute;
  content: '';
  width: 0px;
  height: 0px;
  z-index: 1;
  top: 50%;
  right: -0.5625rem;
  transform: translateY(-50%);
  border-width: 12px 10px 12px 0px;
  border-color: #382620 transparent #382620 #382620;
  border-style: solid;
}

.c-subtitle {
  font-size: 1.75rem;
