.p-top-concept__text {
  margin-top: 3.75rem;
}

.p-top-concept__btn {
  margin-top: 3.75rem;
}

.p-top-gallery {
  background: #EAE0D5;
}

.p-top-gallery__inner {
  padding-top: 5rem;
  padding-bottom: 5rem;
  position: relative;
}

.p-top-gallery__inner::after {
  position: absolute;
  content: "";
  width: 24.375rem;
  height: 20.625rem;
  background-image: url(../img/bg_coffee-beans2.png);
  background-position: right top;
  background-repeat: no-repeat;
  background-size: contain;
  right: -4.0625rem;
  top: -25rem;
  z-index: 3;
}

.p-top-gallery__title {
  text-align: center;
  position: relative;
}

.p-top-gallery__title::after {
  position: absolute;
  content: "";
  width: 16.5625rem;
  height: 8.625rem;
  background-image: url(../img/img_balloon-gallery.svg);
  background-position: right top;
  background-repeat: no-repeat;
  background-size: contain;
  right: calc(50% + 6.25rem);
  top: -6.5625rem;
  z-index: 3;
}

.p-top-gallery__items {
  margin-top: 2.5rem;
  display: grid;
  gap: 1.5rem;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.p-top-gallery__item {
  width: 100%;
  position: relative;
}

.p-top-gallery__item::before {
  padding-top: 100%;
  display: block;
  content: "";
}

.p-top-gallery__item img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  object-position: center;
}

.p-top-gallery__btn {
  text-align: center;
  margin-top: 3.75rem;
}

.p-top-menu {
  position: relative;
}

.p-top-menu::before {
  position: absolute;
  content: "";
  width: 80%;
  height: 83%;
  background: #EAE0D5;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.p-top-menu__inner {
  position: relative;
}

.p-top-menu__inner::after {
  position: absolute;
  content: "";
  width: 16.6875rem;
  height: 18.375rem;
  background-image: url(../img/bg_coffee-leaves2.png);
  background-position: left top;
  background-repeat: no-repeat;
  background-size: contain;
  left: -1.875rem;
  top: -14.25rem;
  z-index: 3;
}

.p-top-menu__title {
  text-align: center;
}

.p-top-menu__content {
  margin-top: 2.5rem;
}

.p-top-menu__subtitle {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.45833;
  padding: 0.25rem 1.25rem;
  border-top: 1px solid #222;
  border-bottom: 1px solid #222;
  position: relative;
}

.p-top-menu__subtitle::before {
  position: absolute;
  content: "";
  width: 100%;
  height: calc(100% + 0.375rem * 2);
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  border-top: 2px solid #222;
  border-bottom: 2px solid #222;
}

.p-top-menu__items {
  margin-top: 1.5rem;
  margin-bottom: 6.25rem;
  display: grid;
  gap: 2.25rem 2.5rem;
  grid-template-columns: 1fr 1fr 1fr;
}

.p-top-menu__btn {
  text-align: center;
  margin-top: 3.75rem;
}

.p-top-news__inner {
  padding-top: 15rem;
  padding-bottom: 10.625rem;
  position: relative;
}

.p-top-news__inner::after {
  position: absolute;
  content: "";
  width: 22.125rem;
  height: 20rem;
  background-image: url(../img/bg_plate.png);
  background-position: left top;
  background-repeat: no-repeat;
  background-size: contain;
  left: -8.125rem;
  top: -9.375rem;
  z-index: 3;
}

@media screen and (max-width: 767px) {
  .p-top-news__inner {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }
}

.p-top-news__title {
  text-align: center;
}

.p-top-news__items {
  margin-top: 2.625rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 3.375rem;
}

.p-top-news__item {
  position: relative;
}

.p-top-news__item:first-child {
  grid-column: 1/3;
  grid-row: 1/3;
}

.p-top-news__btn {
  text-align: center;
  margin-top: 3.75rem;
}

.p-top-set {
  padding-top: 5rem;
  padding-bottom: 5rem;
  background: #EAE0D5;
}

.p-top-set__inner {
  position: relative;
}

.p-top-set__inner::after {
  position: absolute;
  content: "";
  width: 12.5625rem;
  height: 14.25rem;
  background-image: url(../img/bg_coffee-leaves.png);
  background-position: right top;
  background-repeat: no-repeat;
  background-size: contain;
  right: -1.875rem;
  top: -14.25rem;
  z-index: 3;
}

.p-top-set__title {
  text-align: center;
}

.p-top-set__content {
  background: #fff;
  margin-top: 3.75rem;
  padding: 3.75rem 2.5rem 2.5rem;
  position: relative;
}

.p-top-set__content::after {
  position: absolute;
  content: "";
  width: 18.75rem;
  height: 8.75rem;
  background-image: url(../img/img_balloon-special-lunch-set.svg);
  background-position: right top;
  background-repeat: no-repeat;
  background-size: contain;
  right: 0;
  top: -6.25rem;
  z-index: 3;
}

.p-top-set__ribbon {
  display: inline-block;
  position: absolute;
  content: "";
  top: -1.25rem;
  left: 50%;
  transform: translateX(-50%);
  background: #222;
  color: #fff;
  text-align: center;
  padding: 0.25rem 1.1875rem 0.4375rem;
}

.p-top-set__ribbon::before {
  content: '';
  position: absolute;
  width: 2.1875rem;
  bottom: -0.4375rem;
  left: -1.25rem;
  z-index: -2;
  border: 20px solid #222;
  border-left-color: transparent;
}

.p-top-set__ribbon::after {
  content: '';
  position: absolute;
  width: 2.1875rem;
  bottom: -0.4375rem;
  right: -1.25rem;
  z-index: -2;
  border: 20px solid #222;
  border-right-color: transparent;
}

.p-top-set__items {
  display: flex;
  gap: 1.25rem;
}

.p-top-set__item:nth-child(2n) {
  margin-top: 2.5rem;
}

.p-top-set__body {
  margin-top: 3.625rem;
  background: #F7F6F4;
  padding: 1.25rem 4.375rem;
  display: flex;
  gap: 2.5rem;
}

.p-top-set__img {
  width: 100%;
  position: relative;
}

.p-top-set__img::before {
  padding-top: 27.142%;
  display: block;
  content: "";
}

.p-top-set__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: contain;
  object-position: center;
}

.p-top-set__info {
  text-align: right;
}

.p-top-set__head {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.45;
  white-space: nowrap;
}

.p-top-set__price {
  font-family: "Damion", cursive;
  font-size: 3.75rem;
  white-space: nowrap;
  position: relative;
}

.p-top-set__price::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 10px;
  background-image: url(../img/dotted.svg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 3;
  bottom: 0;
}

.p-top-set__time {
  margin-top: 0.5rem;
  font-size: 0.875rem;
  font-weight: 700;
}

.trans {
  opacity: 1;
  transition: 0.3s opacity;
}

.trans:hover {
  opacity: 0.7;
}

.u-mobile {
  display: none;
}

@media screen and (max-width: 767px) {
  .u-mobile {
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .u-desktop {
    display: none;
  }
}

body {
  font-size: 1rem;
}

@media screen and (max-width: 767px) {
  body {
    font-size: 0.875rem;
  }
}

main {
  overflow: hidden;
}
