.p-grand-menu {}

.p-grand-menu__img {
    width: 100%;
    position: relative;
    // &::before {
    //     padding-top: 100%; //height / width
    //     display: block;
    //     content: "";
    // }
    &::after {
        position: absolute;
        content: "";
        border: 8px solid $text-light;
        width: 101%;
        height: 101%;
        top: 0;
        left: 0;
    }
    img {
        // position: absolute;
        // width: 100%;
        // height: 100%;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
        aspect-ratio: 1;
        object-fit: cover;
        // object-position: center;
        width: 100%;
    }
}

.p-grand-menu__title {
    margin-top: rem(12);
    font-size: rem(20);
    font-weight: 700;
    line-height: (29 / 20);
}

.p-grand-menu__price {
    font-family: $font-family-third;
    font-size: rem(32);
    line-height: (44 / 32);
}